* {
  box-sizing: border-box;
}

html,
body,
#root,
.App {
  height: 100%;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;

  .board {
    height: 100%;
    width: 100%;
    background-color: #0b0190;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .card {
      height: calc(100% / 4 - 20px);
      width: calc(100% / 6 - 20px);
      margin: 10px;
      background-position: 50%;
      background-size: cover;

      &.hidden {
        background-image: url('../public/images/card.jpeg');
        background-size: 200%;
        background-position: -107px -116px;
      }
    }
  }
}
